window.EspnOptions = packageJson.EspnOptions;

function gameStartsHere(computer, leaderPool) {
  console.log('Running Start Event')
  computer.player.render('#computer');
  const leaderPoolCssSelectors = ['#leader1', '#leader2', '#leader3'];

  for(let i in leaderPool) {
    const leader = leaderPool[i];
    const selector = leaderPoolCssSelectors[i];

    leader.player.render(selector);

    leader
      .player
      .containerElement
      .setAttribute('player', leader.player.id);
    game.onClick(`${selector} .btn`, {
      active: leader,
      player: leader.player,
      team: leader.team,
      renderId: leader.player.renderId
    })
  }
}

export default function() {
  document.querySelector('#init-game', () => game.init());
  window.EspnApiFactory = EspnApiFactory;
  window.game = new Game();


  game
    .onStart(gameStartsHere)

    .onRoundEnd(function () {
      console.log('Running Round End Event');
      console.log(arguments);
    })


    .onGameOver(function () {
      console.log('Running Game Over Event');
      console.log(arguments)
    })


    .onLoadLocal(function () {
      console.log('Running Load Local Storage Event');
      console.log(arguments)
    })


    .onSaveLocal(function () {
      console.log('Running Save Local Storage Event');
      console.log(arguments)
    })

    .init();
}
